import TweenLite from 'gsap'

export default {
  data: function () {
    return {
      charge: 0,
      tweenedNumber: 0
    }
  },
  computed: {
    animatedNumber: function () {
      return this.tweenedNumber.toFixed(0)
    }
  },
  watch: {
    charge: function (newValue) {
      TweenLite.to(this.$data, 0.333, { tweenedNumber: newValue })
    }
  },
  methods: {
    plus: function () {
      var i = this.charge += 1 * randomInteger(5, 35)
      this.$refs.overlay.style.height = i + '%'

      if (i >= 100) {
        this.charge = 100
        this.$refs.overlay.style.height = 100 + '%'
      }
    },
    minus: function () {
      var i = this.charge -= 1 * randomInteger(5, 35)
      this.$refs.overlay.style.height = i + '%'

      if (i < 0) {
        this.charge = 0
        this.$refs.overlay.style.height = 0 + '%'
      }
    }
  }
}

function randomInteger (min, max) {
  var rand = min - 0.5 + Math.random() * (max - min + 1)
  rand = Math.round(rand)
  return rand
}



